














import { Component, Vue, Prop, Watch } from 'vue-property-decorator';
import { namespace } from 'vuex-class';
import { Topic } from '@/interface';
import CommTopic from '@/components/CommTopic.vue';
import { Tabs } from '@/enum';
import { HomePrevPage, HomeNextPage, HomeState, HomeCurrentPageGetter } from '@/store/interface';
import { Home } from '@/store/types';
import { getTopics } from '@/api';

const HomeModule = namespace('Home');

@Component({
  components: {
    CommTopic,
  },
})
export default class TopicList extends Vue {
  @Prop({required: true})
  private tab!: Tabs;

  private topicList: Array<Topic> = new Array<Topic>();

  @HomeModule.State((state: HomeState) => state.pages)
  private pages!: number[];
  @HomeModule.Mutation(Home.PREV_PAGE)
  private prevPage!: HomePrevPage;
  @HomeModule.Mutation(Home.NEXT_PAGE)
  private nextPage!: HomeNextPage;
  @HomeModule.Getter(Home.getter.currentPage)
  private currentPage!: HomeCurrentPageGetter;

  created() {
    this.getTopics();
  }

  private prev() {
    this.prevPage(this.tab);
    this.getTopics();
  }
  private next() {
    this.nextPage(this.tab);
    this.getTopics();
  }
  private async getTopics() {
    const page = this.currentPage(this.tab);
    console.log('page ', page);

    const data = await getTopics({ tab: this.tab, page });
    if (data) {
      this.topicList = data;
      window.scroll(0, 0);
    }
  }
}
